export default class Splash extends lng.Component{

    static _template(){
        return {
            Logo:{
                x: 960, y: 540, mount:0.5, alpha: 0,
                text:{text:'LOADING..', fontFace:'verdana'}
            }
        };
    }

    _init(){
        this._setState("Loading");
        this._createAnimations();
    }

    _createAnimations(){
        // create animation and store a reference, so we can start / stop / pause 
        this._reveal = this.tag("Logo").animation({
            duration:  2, repeat: -1, actions:[
                {p:'alpha', v:{0:0, 0.5:1, 1:0}}
            ]
        });
        
        // add a finish eventlistener, so we can send a signal
        // to the parent when the animation is completed
        this._reveal.on("finish",()=>{
            this.signal("animationFinished");
        });
    }

    startAnimation(){
        this._start();
    }

    static _states(){
        return [
            class Loading extends this{
                _start(){
                    this._reveal.start();
                }
            },
            class Error extends this{
                $enter(){
                    // signal error & retry
                }
                $exit(){
                    // signal that we exit Error state
                }
            }
        ];
    }
}