import { Lightning, Utils, Settings } from 'wpe-lightning-sdk'
import Api from "./api/Api.js";
import Splash from "./loader/Splash.js"
import Browse from "./browse/Browse.js"
import VPI from './vpi/VPI.js';
import  Hls  from 'hls.js'
import HlsPlayer from './player/HlsPlayer.js';
import Constants from './vpi/Constants.js'
import VideoStats from './components/VideoStats.js'

var videoStats = null;

export default class App extends Lightning.Component {
  static getFonts() {
    return [{ family: 'Regular', url: Utils.asset('fonts/Roboto-Regular.ttf') }]
  }
  
  static _template() {
    return {
      Splash:{
        type: Splash, alpha: 0
      },
      VideoStats: {
        type: VideoStats, x: 120, y: 12, alpha: 1
      },
      Media:{
        type: Browse, x: 100, y: 150, alpha: 0
      }
    }
  }
  
  _construct(){
    window.addEventListener("message", receiveMessage, false);
    window._heartbeat_receiver = this;
    window._api = new Api();
    window._vpi = new VPI();
  }
  
  _init() {
    this._setState("Splash");
    this._initial_settings()
  }
  
  _initial_settings() {
    if (Settings.has('platform', 'background')) {
      const color = Settings.get('platform', 'background');
      document.body.style.backgroundColor = color;
    }
    
    // TODO Move this to a more appropiate place
    // Settings for web platform
    if (Settings.has('platform', 'web') & Settings.get('platform', 'web')) {
      // Initialize the HlsPlayer
      if (Hls.isSupported()) {
        window._player = new HlsPlayer();
      } else {
        console.log("HLS not supported!");
      }
    }
    
    window._vpi.init();
  }
  
  _populate(data){
    data.forEach((props)=>{
      this.tag("Media").data = props;
    });
    this._setState("Media");
  }

  _videoStats(data) {
    this.tag("VideoStats").data = data;
  }
  
  $onItemSelect({item}){
    window._vpi.play(item)
  }
  
  static _states(){
    return [
      class Splash extends this{
        $enter(){
          this.tag("Splash").setSmooth("alpha",1);
          this.tag("Splash").startAnimation();
          window._api.getMedia().then((data)=>{
            this._populate(data);
          })
        }
        $exit(){
          this.tag("Splash").setSmooth("alpha",0);
        }
      },
      class Media extends this{
        $enter(){
          this.tag("Media").setSmooth("alpha",1);
        }
        $exit(){
          this.tag("Media").setSmooth("alpha",0);
        }
        _getFocused(){
          return this.tag("Media");
        }
      }
    ]
  }
}

function receiveMessage(event){
  //TODO Verify the origin of the message
  let message;
  try {          
    message = JSON.parse(event.data);
  } catch (error) {
  }
  if (message) {
    if (message.type == Constants.PLAYER_TYPE) {
      switch(message.event) {
        case Constants.HEARTBEAT_EVENT:
        videoStats = (message.data);
        window._heartbeat_receiver._videoStats(videoStats);
        break;
      }
    }
  }
}