
import Constants from '../vpi/Constants.js'

export default class VPI {

    _postMessage(message){
        window.postMessage(JSON.stringify(message), "*")
    }
    
    init() {
        let message = {type: Constants.PLAYER_TYPE, event: Constants.INIT_EVENT};
        this._postMessage(message)
    }
    
    play(item) {
        let message = {type: Constants.PLAYER_TYPE, event: Constants.PLAY_EVENT, data: item};
        this._postMessage(message);
    }

}