import { Utils } from 'wpe-lightning-sdk' 

export default class MainSliderItem extends lng.Component{
    static _template(){
        return {
            rect: true, color: 0x00000000, w: 370, h: 556, scale:1,
            transitions:{scale:{duration:0.3, delay:0.05}}
        }
    }

    set item(v){
        this._item = v;
        this.patch({
            Wrapper: {
                rect: true, color: 0x00000000, w: 370, h: 556, scale:1,
                CoverImage: {
                    x: w=>w/2, y: h=>h/2, mount: 0.5,
                    texture: {type: lng.textures.ImageTexture, src: Utils.asset(v.imageUri), resizeMode: {type: 'contain', w: 370, h: 556}}
                },
                CoverText: {
                    text: {text: v.name, wordWrapWidth: 370, textOverflow: 'ellipsis', maxLines: 1,}
                }
            }
        });
    }

    get item(){
        return this._item;
    }

    _focus(){
        this.setSmooth("scale",1.1);
    }

    _unfocus(){
        this.setSmooth("scale",1);
    }

    static _states(){
        return [

        ]
    }

    static get width(){
        return 385;
    }

    static get height(){
        return 556;
    }
}