import { Settings } from 'wpe-lightning-sdk'
import Hls from 'hls.js'
import Constants from '../vpi/Constants.js'

var playerStats = {};
var hls = null;

export default class HlsPlayer {
    
    constructor() {
        window.addEventListener("message", this.receiveMessage, false);
    }
    
    _initDOM() {
        let container = document.createElement('div')
        container.id = "container";
        container.width = Settings.get('app', "stage").w;
        container.height = Settings.get('app', "stage").h;
        this._video = document.createElement('video');
        this._video.id = "video";
        this._video.width = Settings.get('app', "stage").w;
        this._video.height = Settings.get('app', "stage").h;
        container.appendChild(this._video);
        document.body.appendChild(container);
    }
    
    _play(item) {
        if (hls) {
            clearInterval(this.heartbeatSignal);
            video.pause();
            hls.stopLoad();
            hls.destroy();
            hls = null;
        }
        playerStats = {};
        playerStats.curentDropped = 0;
        playerStats.currentDecoded = 0;
        playerStats.totalDroppedFrames = 0;
        hls = new Hls();
        this._initHlsListeners();
        hls.attachMedia(this._video);
        
        hls.loadSource(item.uri);
        
        this.heartbeatSignal = window.setInterval(this.heartbeat, 1000);
    }
    
    _initHlsListeners() {
        
        hls.on(Hls.Events.MANIFEST_PARSED, function (event, data) {
            console.log("Manifest loaded. Found " + data.levels.length + " quality level");
            video.play();
        });
        
        hls.on(Hls.Events.FPS_DROP, function (event, data) {
            playerStats.curentDropped = data.curentDropped;
            playerStats.currentDecoded = data.currentDecoded;
            playerStats.totalDroppedFrames = data.totalDroppedFrames;
        });
    }
    
    heartbeat() {
        let levels = hls.levels;
        if (levels) {
            let level = hls.levels[hls.currentLevel];
            if (level) {
                playerStats.bitrate = level.bitrate;
                playerStats.videoWidth = level.width;
                playerStats.videoHeight = level.height;
            }
            playerStats.levels = hls.levels.length;
            playerStats.currentLevel = hls.currentLevel + 1;
        }
        playerStats.bandwidth = hls.bandwidthEstimate;
        
        let message = {type: Constants.PLAYER_TYPE, event: Constants.HEARTBEAT_EVENT, data: playerStats};
        window.postMessage(JSON.stringify(message), "*")
    }
    
    receiveMessage(event){
        //TODO Verify the origin of the message
        let message;
        try {          
            message = JSON.parse(event.data);
        } catch (error) {
        }
        if (message) {
            if (message.type == Constants.PLAYER_TYPE) {
                switch(message.event) {
                    case Constants.INIT_EVENT:
                    window._player._initDOM();
                    break;
                    case Constants.PLAY_EVENT:
                    window._player._play(message.data);
                    break;
                    default:
                    break;
                }
            }
        }
    }
}