export default class Api{
    getMedia(){
        const promises = [
            // this._getMovies(),
            // this._getTvShows()
            this._getRemoteMedia()
        ];
        return Promise.all(promises);
    }

    _getMovies(){
        return fetch("./static/movies.json").then((response)=>{
            return response.json();
        }).then((data)=>{
            return {ref:"Movies", data};
        });
    }

    async _getTvShows(){
        const stream = await fetch("./static/series.json");
        const data = await stream.json();
        return {ref:"TvShows", data};
    }

    async _getRemoteMedia(){
        const stream = await fetch("https://raw.githubusercontent.com/rolandoamarillo/files/master/iptv/live_colombia.json");
        const data = await stream.json();
        return data;
    }
}

