export default class VideoStats extends lng.Component {
    
    static _template(){
        return {
            Bandwidth:{
                text:{text:"Bandwidth: 0 Kbps", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 0
            },
            Levels:{
                text:{text:"Levels: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 16
            },
            Level:{
                text:{text:"Level: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 32
            },
            Bitrate:{
                text:{text:"Bitrate: 0", fontSize:16, fontFace:"verdana",shadow: true, shadowColor: 0xFF000000}, y: 48
            },
            VideoWidth:{
                text:{text:"Width: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 64
            },
            VideoHeight:{
                text:{text:"Height: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 80
            },
            Dropped: {
                text:{text:"Dropped: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 96
            },
            Decoded: {
                text:{text:"Decoded: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 112
            },
            TotalDropped: {
                text:{text:"Total Dropped: 0", fontSize:16, fontFace:"verdana", shadow: true, shadowColor: 0xFF000000}, y: 128
            }
        }
    }
    
    set data(v){
        const {bandwidth, levels, currentLevel, bitrate, videoWidth, videoHeight, curentDropped, currentDecoded, totalDroppedFrames} = v;
        this.patch({
            Bandwidth:{
                text:{text:"Bandwidth: " + Math.round(bandwidth / 1024) + " Kbps"}
            },
            Levels:{
                text:{text:"Levels: " + levels}
            },
            Level:{
                text:{text:"Level: " + currentLevel}
            },
            Bitrate:{
                text:{text:"Bitrate: " + bitrate}
            },
            VideoWidth:{
                text:{text:"Width: " + videoWidth}
            },
            VideoHeight:{
                text:{text:"Height: " + videoHeight}
            },
            Dropped:{
                text:{text:"Dropped: " + curentDropped}
            },
            Decoded:{
                text:{text:"Decoded: " + currentDecoded}
            },
            TotalDropped:{
                text:{text:"Total Dropped: " + totalDroppedFrames}
            }
        });
    }
    
}